
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs-compat';
import { AngularFireDatabase, AngularFireList } from 'angularfire2/database';
import * as firebase from 'firebase';
import { LoadingController  } from 'ionic-angular';
import { CamaraProvider } from '../camara/camara';


@Injectable()
export class ImagenFirebaseProvider {

  constructor(private fotos: CamaraProvider, 
    private afDB: AngularFireDatabase,
    private loadingCtrl: LoadingController) {
 
  }

  //areglo de imagenes
  imagenes:ArchivoSubir[] = [];
  //ultima inserccion
  lastkey:String;

  cargar_imagen_firebase( archivo:ArchivoSubir ){
    try {
      let promesa = new Promise((resolve, reject)=>{
      //this.mostrar_toast('Cargando...');
      let loading = this.loadingCtrl.create({
        content: "Espere por favor",
      });
      loading.present();
      //referencia al Storage de Firebsae
      let storRef = firebase.storage().ref();
      let nombreArchivo:string = new Date().valueOf().toString();
      //guatdar la imagen en Storage dentro de una carpeta 'img' en fireBase
      /*
      let uploadTask: firebase.storage.UploadTask = 
        storRef.child('img/'+nombreArchivo)
          .putString( archivo.img+'', 'base64', { contentType: 'image/jpg' } );
      */
      //guatdar la imagen en Storage en fireBase (solucion para conpartir la imgane por facebook)
      let uploadTask: firebase.storage.UploadTask = 
        storRef.child(nombreArchivo+'.jpg')
        .putString( archivo.img+'', 'base64', { contentType: 'image/jpg' } );    
          //muestra cuando el archivo se esta subiendo
          uploadTask.on( firebase.storage.TaskEvent.STATE_CHANGED, 
            ()=>{}, //saber el % de cuantos Mbis se han subido
            ( error ) => {
                loading.dismiss();
                //console.log("Error en la carga de la foto: ", JSON.stringify(error));
                //this.mensaje.message(JSON.stringify(error));
                resolve();
            },
            ()=>{
             // TODO Bien!!
              //console.log('Archivo subido');
              //guardar la img en firebase
              loading.dismiss();
              console.log('</b> Imagen cargada correctamente.');

              uploadTask.snapshot.ref.getDownloadURL().then(downloadURL => {
                this.crear_post(archivo.nombre, archivo.apellidos, downloadURL, nombreArchivo);
                this.fotos._imgPreview="";
              }); 
              //let url = uploadTask.snapshot.downloadURL;
              //this.crear_post(archivo.titulo, url, nombreArchivo);
              resolve();
            }
          )
    });
    return promesa;
    } catch (error) {
      console.log('Error al cargar la imagen');
    }
    
  }

  private crear_post( nombre:String, apellidos:String, url:String, nombreArchivo:String){
    let post: ArchivoSubir = {
      img: url,
      nombre: nombre,
      apellidos: apellidos,
      key: nombreArchivo,
    };
    //console.log(JSON.stringify(post));

    //subir el arhivo a la BD firebase
    //this.afDB.list('/post').push(post); //puede ser una manera de agegarlo con un nombre por defecto
    try {
      //guardar con nombre personalizado es como un ID
      this.afDB.object('/post/'+nombreArchivo).update(post);
      this.imagenes.push(post);
    } catch (error) {
      console.log('Error al publicar');
    }
  }  

  publicaciones: Observable<any[]>;
  obtenerImagenes() {
    try {
      this.publicaciones = this.afDB.list('post').valueChanges();
    } catch (error) {
      console.log('Error al obtener los datos');
    }
  }

}

export interface ArchivoSubir{
  nombre?: String;
  apellidos?:String;
  img?:String;
  key?: String;
  
}



